.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--dark);
}
.nav {
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}

.nav__logo,
.nav__toggle {
  font-weight: var(--font-medium);
  cursor: pointer;
  font-size: 1.3rem;
}

span.smallFont {
  font-size: 1.25rem;
}

.nav__list {
  display: flex;
  column-gap: 2rem;
}

.nav__logo {
  color: var(--accent);
  font-weight: var(--font-medium);
}
.nav__toggle {
  color: var(--light);
  font-weight: var(--font-medium);
}

.nav__list {
  display: flex;
  column-gap: 2rem;
}

.nav__icon,
.nav__close,
.nav__toggle {
  width: 24px;
  height: auto;
  display: none;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1;
  font-size: var(--small-font-size);
  color: var(--light);
  font-weight: var(--font-medium);
  transition: 0.3s;
  cursor: pointer;
}

/* Active Link */
.nav__item .active,
.nav__link:hover {
  color: var(--accent);
}

/*=============== BREAKPOINTS ===============*/

/* For medium devices */
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
    background-color: var(--accent);
  }

  .nav {
    height: var(--header-height);
  }
  .nav__menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--dark);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0px -1px 4px rgba(255, 255, 255, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.3s;
  }

  .show-menu {
    bottom: 0;
  }
  .nav__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }

  .nav__icon {
    width: 1.425rem;
    margin-bottom: 0.35rem;
  }

  .nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
  }
  .nav__close path {
    stroke: var(--light) !important;
  }
  .nav__close:hover path {
    stroke: var(--accent) !important;
  }
  .nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }
  .nav__icon,
  .nav__close,
  .nav__toggle {
    display: block;
  }
}

/* For small devices */
@media screen and (max-width: 576px) {
  .nav__menu {
    padding: 2rem 0.25rem 4rem;
  }

  .nav__list {
    column-gap: 0;
  }
}
