@media only screen and (max-width: 768px) {
  .biofili .item .left .img {
    height: initial;
    max-height: 250px;
    position: relative;
    width: 100%;
    height: 250px;
  }
  .biofili .item .left .img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 300px;
  }
}
