.theme-btn {
  height: 20px;
  width: 40px;
  background-color: var(--accent);
  border-radius: 80px;
  overflow: hidden;
  cursor: pointer;
  transition: 0.3s;
  display: flex;
  align-items: center;
}
.theme-btn:before {
  content: "";
  margin: 0 1px;
  height: 18px;
  width: 18px;
  background: var(--dark);
  border-radius: 80px;
  transition: 0.5s;
}

body.light-theme .theme-btn {
  background-color: var(--light);
  transition: all 0.6s;
}
body.light-theme .theme-btn::before {
  transform: translateX(112%);
  background: var(--dark);
  transition: all 0.6s;
}

/*=============== BREAKPOINTS ===============*/

/* For medium devices */
@media screen and (max-width: 768px) {
  .theme-btn {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: 1rem;
  }
}
