.contact__button {
  color: var(--accent);
  font-size: var(--small-font-size);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  column-gap: 0.25rem;
}

.contact__button-icon {
  font-size: 1rem;
  transition: 0.3s;
}

.contact__button:hover .contact__button-icon {
  transform: translate(0.25rem);
}

.contact__form {
  width: 360px;
}

.contact__form-div {
  position: relative;
  margin-bottom: var(--m-2);
  height: 4rem;
  transition: margin 0.3s ease-in-out;
}
.contact__form-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid rgba(227, 220, 220, 0.3);
  background: none;
  color: var(--accent);
  outline: none;
  border-radius: 0.75rem;
  padding: 1.5rem;
  z-index: 1;
}
.contact__form-input::placeholder {
  color: var(--light);
  opacity: 0.8;
}

.contact__form-tag {
  position: absolute;
  top: -1rem;
  left: 1.25rem;
  background-color: var(--dark);
  opacity: 0.8;
  color: var(--accent);
  border-radius: 0.4rem;
  font-size: 1rem;
  padding: 0.3rem;
  z-index: 10;
}

.contact__form-area {
  height: 11rem;
}

.contact__form-area textarea {
  resize: none;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
  .contact__container {
    column-gap: 3rem;
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .contact__form {
    margin: 0 auto;
  }
}

@media screen and (max-width: 576px) {
  .contact__form {
    width: 100%;
  }
}
